import { PublicationModel } from "../Models/PublicationModel";

const base_api = 'https://pasdecarrierealaamondrans.go.yo.fr'

export interface IAPIPublicationsRepository {
  listPublications(folderId: string): Promise<PublicationModel[]>,
  getPublications(fileId: string): Promise<PublicationModel>,
}

export class APIPublicationsRepository implements IAPIPublicationsRepository {
  async listPublications(folderId: string): Promise<PublicationModel[]> {
    const request = await fetch(`${base_api}/index.php?publications`)
    const response = await request
    return response.json()
  }
  async getPublications(fileId: string): Promise<PublicationModel> {
    const request = await fetch(`${base_api}/index.php?get&id=${fileId}`)
    const response = await request
    // console.log(response)
    return response.json()
    // return await request.json()
  }

}
