import { Box, FilledInput, FormControl, FormHelperText, IconButton, InputAdornment, InputBase, InputLabel, OutlinedInput, Paper, TextField, } from "@mui/material";
import SendIcon from '@mui/icons-material/Send';

import './FooterBar.css';
import { useState } from "react";

type DataInput = {
    email: string,
    error: '',
    valid: true
}

export default function FooterBar() {
    const [dataInput, setDataInput] = useState<DataInput>();

    const handleSubmit = (e: any, onSubmit: Function) => {
        e.preventDefault()
        onSubmit()
    }

    const onChanged = (e: any) => {
        console.log(e)

        setDataInput({email: e.target.value, ...dataInput} as DataInput)
    }

    const validate = () => {
        console.log("Data: ", dataInput?.email)
        const valid = dataInput !== undefined || dataInput!.email.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i) || dataInput!.email !== undefined

        setDataInput({
            valid: valid,
            ...dataInput
        } as DataInput)
    }

    const onSubmit = () => {
        validate()

        if (dataInput?.valid === true) {
            console.log(dataInput.email)
        }
    }

    return (
        <Box className="footer">

            <div className="newsletter">
                <h3>Recevoir des informations</h3>
                <form onSubmit={e => handleSubmit(e, onSubmit)}
                >
                    <FormControl variant="outlined" fullWidth>
                        <InputLabel >Saisissez votre adresse email</InputLabel>
                        <FilledInput

                            value={dataInput?.email}
                            onChange={onChanged}
                            style={{ backgroundColor: '#fff' }}
                            type='text'
                            endAdornment={
                                <InputAdornment position="end">
                                    <IconButton aria-label="search" onClick={onSubmit}>
                                        <SendIcon />
                                    </IconButton>
                                </InputAdornment>
                            }
                        />
                        <FormHelperText error={true} hidden={!dataInput?.valid}>
                            Veuillez entrer une adresse email valide
                        </FormHelperText>
                    </FormControl>
                </form>
            </div>
            <div className="legals-links">
                <div>Mentions légales</div>
                <div>Politique en matière de cookies</div>
                <div>Politique de confidentialité</div>
                <div>Conditions d’utilisation</div>
            </div>
            <div className="copyright">
                © Pas de carrière à Laà-Mondrans - 2024
            </div>
        </Box >
    );
}